import '../../App.css';
import './weatherFuture.css';

export const WeatherFuture = (props) => {
    console.log(props);
    return (
        <div className="weather-future">
            {
                props.weatherFutureData.list.filter(el => props.weatherFutureData.list.indexOf(el) % 8 == 0).map(item =>
                    <div className="weather-future-card" key={item.dt}>
                        <span>
                            {new Date(item.dt * 1000).toLocaleDateString().slice(0, 5)} {new Date(item.dt * 1000).toLocaleTimeString().slice(0, 5)}
                        </span>
                        <div className='weather-future-temp-img'>
                            <span>
                                {parseInt(item.main.temp)} {props.unitsTitle}
                            </span>
                            <img src={`https://openweathermap.org/img/wn/${item.weather[0].icon}@2x.png`} alt="" />
                        </div>
                        <div className='weather-future-description'>
                            <span>
                                {item.weather[0].description.toUpperCase()}
                            </span>
                            <span>
                                Відчувається як {parseInt(item.main.feels_like)} {props.unitsTitle}
                            </span>
                        </div>
                    </div>

                )
            }
        </div>
    )
}