import '../../App.css';
import './weatherNow.css';

export const WeatherNow = (props) => {
    
    return (
        <section className='weather-now'>

            <div className="temp-description">
                <span className='temperature'>{parseInt(props.weatherNowData.main.temp)} {props.unitsTitle}</span>
                <div>
                    <img src={`https://openweathermap.org/img/wn/${props.weatherNowData.weather[0].icon}@2x.png`} alt="weather icon" />
                </div>
                <div className="description">
                    <p>{props.weatherNowData.weather[0].description.toUpperCase()}</p>
                    <p>Відчувається як {parseInt(props.weatherNowData.main.feels_like)} {props.unitsTitle}</p>
                </div>
            </div>

            <div className="additions">
                <ul>
                    <li>
                        <span>Атмосферний тиск: </span>
                        <span>{props.weatherNowData.main.pressure} mb</span>
                    </li>
                    <li>
                        <span>Вітер: </span>
                        <span>{parseInt(props.weatherNowData.wind.speed)} km/h</span>
                    </li>
                    <li>
                        <span>Видимість: </span>
                        <span>{props.weatherNowData.visibility.toString().slice(0, -3)} km</span>
                    </li>
                    <li>
                        <span>Вологість: </span>
                        <span>{props.weatherNowData.main.humidity}%</span>
                    </li>
                </ul>
            </div>

        </section>
    )
}